<template>
  <div class="zd-common-component">
    <div class="zd-common-clearFix">
      <Button type="primary" class="zd-common-fl zd-common-pl11 zd-common-pr11 zd-common-mr10" custom-icon="zd-common-icon-add" @click="showAddModal">
        
      </Button>
      <Button class="zd-common-fl zd-common-pl11 zd-common-pr11 zd-common-mr10"
              custom-icon="zd-common-icon-sort" @click="sortHandle">
        
      </Button>

    </div>
    <div class="zd-common-mt16">
      <Table ref="currentRowTable" border :columns="columns" :data="data"></Table>
      <!--<div class="zd-common-tc zd-common-mt20">
        <Page :total="total" :current="pageNum" :page-size="pageSize" @on-change="changePage" @on-page-size-change="changePageSize" show-sizer show-elevator show-total></Page>
      </div>-->
    </div>
    <!---->
    <Modal v-model="addModal" :mask-closable="false" class="zd-cm-form">
      <h3 slot="header" class="zd-common-modal-header">
        <span>{{ modalTitle }}</span>
      </h3>
      <Form ref="addForm" :model="addForm" :rules="ruleValidate" :label-width="110">
        <FormItem label=""  prop="type">
          <Select v-model="addForm.type" placeholder="" class="zd-common-w300">
            <Option v-for="item in typeArr" :value="item.id" :key="item.id">{{ item.name }}</Option>
          </Select>
        </FormItem>
        <FormItem label="" prop="displayName">
          <Input v-model.trim="addForm.displayName" placeholder="" :maxlength="50" class="zd-common-w300" @on-blur="transCode"></Input>
        </FormItem>
        <FormItem label="">
          <Input v-model.trim="addForm.enName" placeholder="" :maxlength="50"
                 class="zd-common-w300"></Input>
        </FormItem>
        <FormItem label=""  prop="code">
          <Input v-model.trim="addForm.code" placeholder="" :maxlength="50" class="zd-common-w300"></Input>
        </FormItem>
        <FormItem label=""  prop="status">
          <Select v-model="addForm.status" placeholder="" class="zd-common-w300">
            <Option value="1"></Option>
            <Option value="0"></Option>
          </Select>
        </FormItem>
      </Form>
      <div slot="footer" class="zd-common-modal-footer zd-common-pt0">
        <Button type="primary" class="zd-common-mr35 zd-common-w100 zd-common-h32 zd-common-lh8"
                @click="handleSubmit('addForm')"
                :loading="saveLoading">
          <span v-if="!saveLoading"></span>
          <span v-else>...</span>
        </Button>
        <Button @click="addModal = false" class="zd-common-w100 zd-common-h32 zd-common-lh8"></Button>
      </div>
    </Modal>
    <!---->
    <Modal v-model="delModal" :mask-closable="false" :width="360">
      <h3 slot="header"></h3>
      <div class="zd-common-tc">
        <div class="zd-common-modal-waring zd-common-auto"></div>
        <p class="zd-common-primary-c zd-common-font-size-large zd-common-mt15"></p>
      </div>
      <div slot="footer" class="zd-common-modal-footer zd-common-pt10">
        <Button type="primary" class="zd-common-mr24 zd-common-w100 zd-common-h32 zd-common-lh8" :loading="saveLoading" @click="confirmDel">
          <span v-if="!saveLoading"></span>
          <span v-else>...</span>
        </Button>
        <Button @click="delModal = false" class="zd-common-w100 zd-common-h32 zd-common-lh8"></Button>
      </div>
    </Modal>
    <!--  -->
    <Modal v-model="sortModal" :mask-closable="false">
      <h3 slot="header" class="zd-common-modal-header">
        <span></span>
      </h3>
      <vue-scroll>
        <draggable v-model="sortList" :options="{'ghostClass':'dic-ghost','forceFallback':true,'fallbackClass':'dic-dragClass'}">
          <transition-group class='dic-drag'>
            <div v-for="item in sortList" :key="item.id">
              <Icon type="md-move"/>
              {{item.type}} - {{item.displayName}}
            </div>
          </transition-group>
        </draggable>
      </vue-scroll>

      <div slot="footer" class="zd-common-modal-footer">
        <Button type="primary" class="zd-common-mr35 zd-common-w100 zd-common-h32 zd-common-lh8" @click="sortSubmit"
                :loading="saveLoading">
          <span v-if="!saveLoading"></span>
          <span v-else>...</span>
        </Button>
        <Button @click="sortModal = false" class="zd-common-w100 zd-common-h32 zd-common-lh8"></Button>
      </div>
    </Modal>
  </div>
</template>
<script>
  import { transformPY,utils } from "../../../comm/script/utils";
  import draggable from 'vuedraggable';
  export default {
    components: {
      draggable
    },
    data() {
      const validateCode = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else {
          if(utils.regCode.test(value)){
            BaseAxios(
              {
                method: 'post',
                url: '/sr/storeroomData!validateGoodsSupplierCode.action',
                data: {
                  validateValue:value,
                  excludeValue:this.oldCode //
                }
              })
              .then((response) => {
                if(!response.data){
                  callback(new Error(''));
                }
                callback();
              })
              .catch((error) => {
                callback(new Error(''));
                console.log(error);
              });
          }else{
            callback(new Error(utils.regCodeMsg));
          }
        }
      }
      return {
        //
        typeArr:[],
        addModal:false,
        oldCode:'',
        addForm:{
          type:'',
          displayName:'',
          enName:'',
          code:'',
          status:'1',
        },
        ruleValidate: {
          type: [
            { required: true, message:'', trigger: 'change' }
          ],
          displayName: [
            { required: true, message:'', trigger: 'blur' }
          ],
          code: [
            { required: true, message:'', trigger: 'blur' }
          ],
          /*code: [
            { required: true, validator:validateCode, trigger: 'blur' }
          ],*/
          status: [
            { required: true, message:'', trigger: 'change' }
          ],
        },
        saveLoading:false,
        modalTitle:'',
        //
        editId:'',
        //
        columns: [
          {
            type: 'index',
            align: 'center',
            title: '',
            width:65
          },
          {
            title: '',
            align: 'center',
            key: 'type',
          },
          {
            title: '',
            align: 'center',
            key: 'displayName'
          },
          {
            title: '',
            align: 'center',
            key: 'enName',
          },
          {
            title: '',
            align: 'center',
            key: 'code',
          },
          {
            title: '',
            align: 'center',
            key: 'status',
          },
          {
            title: '',
            align: 'center',
            key: 'action',
            width:160,
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props:{
                    type:'primary',
                    ghost:true,
                  },
                  style: {
                    height: "28px",
                    padding:'0 15px 1px',
                    marginRight:'10px'
                  },
                  on: {
                    click: () => {
                      this.showModifyModal(params.index);
                    }
                  }
                }, ''),
                h('Button', {
                  props:{
                    type:'error',
                    ghost:true,
                  },
                  style: {
                    height: "28px",
                    padding:'0 15px 1px'
                  },
                  on: {
                    click: () => {
                      this.showDelModal();
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        data: [],
        //
        delModal:false,
        delId:'',
        //
        sortModal:false,
        sortList:[]
      }
    },
    created(){
      this.getIntoOutDictionaryValueList();
      this.getDictionaryKindMap();
    },
    methods:{
      //
      getDictionaryKindMap() {
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!getDictionaryKindMap.action',
          data:{},
        })
          .then((response) => {
            this.typeArr = response.data;
          })
          .catch((error) => {
            this.postErrorTip('');
            console.log(error);
          });
      },
      //
      sortHandle(){
        this.sortModal = true;
        this.sortList = this.data.slice();
      },
      sortSubmit(){
        this.saveLoading = true;
        let id = this.sortList.map((item)=>{
          return item.id;
        });
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!dictionaryValueSaveSort.action',
          data:{
            ids:id.join(',')
          },
        })
        .then((response) => {
          this.saveLoading = false;
          this.sortModal = false;
          this.data = this.sortList;
          this.postSuccessTip('');
        })
        .catch((error) => {
          this.saveLoading = false;
          this.sortModal = false;
          this.postErrorTip('');
          console.log(error);
        });
      },
      //code
      transCode(){
        this.addForm.code = transformPY(this.addForm.displayName);
      },
      //
      showAddModal(){
        this.modalTitle = "";
        this.addModal = true;
        this.editId = '';
        this.addForm.enName = '';
        this.$refs['addForm'].resetFields();
      },
      saveOrUpdateDictionaryValue(type,displayName,enName,code,status,editId) {
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!saveOrUpdateDictionaryValue.action',
          data:{
            "dictionaryValue.dictionary.id":type,
            "dictionaryValue.value":displayName,
            "dictionaryValue.valueI18n":enName,
            "dictionaryValue.code":code,
            "dictionaryValue.status":status,
            "dictionaryValue.id":editId,
          },
        })
          .then((response) => {
            this.saveLoading=false;
            this.addModal=false;
            this.postSuccessTip('');
            this.getIntoOutDictionaryValueList();
          })
          .catch((error) => {
            this.saveLoading=false;
            this.addModal=false;
            this.postErrorTip('');
            console.log(error);
          });
      },
      //
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.saveLoading=true;
            this.saveOrUpdateDictionaryValue(this.addForm.type,this.addForm.displayName,this.addForm.enName,this.addForm.code,this.addForm.status,this.editId);
          }
        })
      },

      //
      showModifyModal(index){
        this.addModal = true;
        this.modalTitle = "";
        this.editId = this.data[index].id;
        this.addForm.type = this.data[index].dicId;
        this.addForm.displayName = this.data[index].displayName;
        this.addForm.enName = this.data[index].enName;
        this.addForm.code = this.data[index].code;
        this.addForm.status = this.data[index].statusCode;
      },

      //
      showDelModal(id){
        this.delId = id;
        this.delModal = true;
      },
      confirmDel(){
        this.saveLoading = true;
        this.deleteHandle(this.delId);
      },
      deleteHandle() {
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!getDictionaryKindMap.action',
          data:{},
        })
          .then((response) => {
            this.saveLoading = false;
            this.delModal = false;
            this.postSuccessTip('');
            this.getIntoOutDictionaryValueList();
          })
          .catch((error) => {
            this.delModal = false;
            this.postErrorTip('');
            console.log(error);
          });
      },
      //
      getIntoOutDictionaryValueList() {
        this.data = [];
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!getIntoOutDictionaryValueList.action',
          data:{},
        })
          .then((response) => {
            let aData = response.data;
            if(aData && aData.length>0){
              aData.map((item)=>{
                this.data.push({
                  id:item.id,
                  dicId:item.dictionaryId,
                  type:item.kind,
                  displayName:item.value,
                  enName:item.englishName,
                  code:item.code,
                  status:item.enableFlagName,
                  statusCode:item.enableFlag,
                })
              })
            }
            this.total = aData.totalCount;
            this.selectLoading = false;
          })
          .catch((error) => {
            this.selectLoading = false;
            this.postErrorTip('');
            console.log(error);
          });
      },

    },
    mounted(){}
  }
</script>
<style lang="less" scoped>
  .dic-drag{display: block;max-height: 275px; border:1px solid #eee; border-bottom: none;}
  .dic-drag > div{ line-height: 36px; font-size: 16px; border-bottom: 1px solid #eee;padding:0 10px; cursor: move;}
  .dic-drag > div:hover{background: #f6f6f6;}
  .dic-ghost{border:1px dashed #eee; color:#1c90f3;}
  .dic-dragClass{border:1px dashed #ccc; background: #fff; opacity: .8}
</style>



